<template>
    <div>
        <div class="sb-tree fl" v-show="TreeShow">
            <div class="sb-search">
                <Input size="small" v-model="value" placeholder="" style="width: 200px;margin-right:14px;" />
                <Button type="primary" size="small"></Button>
            </div>
            <div class="tree-con">
                
                <Tree :data="data1" @on-select-change="pingwei"></Tree>
            </div>
            <Icon @click.native="suoqi" class="suoqi" type="ios-arrow-back" />
        </div>
        <Icon v-show="zkShow" @click.native="zhankai" class="zhankai" type="ios-arrow-forward" />
    </div>
</template>
<script>
    export default {
        name: '',
        data() {
            return {
                TreeShow: true,
                zkShow: false,
                data1: [{
                    title: '220kV',
                    expand: true,
                    children: [{
                            title: '',
                            expand: true,
                        },
                        {
                            title: '1# ',
                            expand: true,
                        },
                        {
                            title: '2# ',
                            expand: true,
                        },
                        {
                            title: '220kV',
                            expand: true,
                        },
                        {
                            title: '220kV',
                            expand: true,
                        }, {
                            title: '110kV184',
                            expand: true,
                        },
                        {
                            title: '110kV187',
                            expand: true,
                        }
                    ]
                }]
            }
        },
        methods: {
            suoqi: function (params) {
                this.TreeShow = false;
                this.$store.commit('suoqi', '100%');
                this.zkShow = true;
            },
            zhankai: function (params) {
                this.TreeShow = true;
                this.$store.commit('suoqi', '74%');
                this.zkShow = false;
            },
            pingwei: function (params) {
                if (params) {
                    let data = params[0]['title'];
                    if (data == "") {
                        this.$router.push('pingwei');
                    }
                }
            }
        }
    }
</script>
<style lang='less' scoped>
    .sb-tree {
        position: relative;
        width: 26%;
        padding: 10px 0px;
        font-size: 16px !important;
    }

    .tree-con {
        width: 254px;
        border: 2px solid #dcdee2;
        height: 460px;
        overflow: scroll;
        text-align: left;
        padding: 10px;
        margin-left: 10px;
        margin-top: 10px;
    }

    .suoqi {
        position: absolute;
        font-size: 48px;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
    }

    .zhankai {
        position: fixed;
        font-size: 48px;
        top: 56%;
        left: 10px;
        transform: translateY(-50%);
        z-index: 2;
    }

    /* */

    .tree-con::-webkit-scrollbar {
        width: 5px;
        /**/
        height: 5px;
        margin-left: 5px;
        /**/
    }

    /* +*/

    .tree-con::-webkit-scrollbar-track {
        border-radius: 2.5px;
        /**/
        background-color: #e6e6e6;
        /**/
    }

    /* +*/

    .tree-con::-webkit-scrollbar-thumb {
        border-radius: 2.5px;
        /**/
        background-color: #5e5e5e !important;
        /**/
    }
</style>